.quiz {
  cursor: pointer;
  position: relative;
  width: 200px;
  letter-spacing: 2px;
  -webkit-font-smoothing: always; 
  perspective: 600px;
}

.quiz__question, .quiz__answer {
  padding-bottom: 40px;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  transition: transform .6s;
  backface-visibility: hidden;
  background: url() no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #1a1a1a;
  border: 5px solid #2c2c2c;
  box-shadow: -4px 2px 10px black;
  text-align: center;
  border-bottom: 10px solid #af2b35;
  transform: rotateY(25deg);
  height: 100%;
  transform-style: preserve-3d;
}

.quiz__answer h1 {
  font-size: 1.2em;
}

.quiz__answer h1 span {
  font-size: 0.5em;
}

h1 {
  font-size: 1.10em;
 line-height: 1.5em;
  padding: 10px;
  color: #dadada;
  text-align: left;
  margin: 0;
  display: block;
}

small {
  margin: 0;
  padding: 0px;
  position: relative;
  bottom: 10px;
}

.icon {
  font-size: 1.4em;
  display: block;
  font-weight: bold;
  padding-bottom: 10px;
  margin-bottom: 20px;
}

h1 span i {
  position: relative;
  background: #af2b35;
  padding: 5px 18px;
  position: relative;
  left: -10px;
  color: white;
}
.correct-answer:hover{
  color:rgb(255, 127, 0);
}
.quiz__answer {
  padding-bottom: 50px;
  border-radius: 10px;
  box-sizing: border-box;
  top: 0;
  height: 100%;
  line-height: 2em;
  position: absolute;
  left : 0;
  margin: 0;
  transition: transform .6s;
  width: 100%;
  z-index: -1; 
  transform: rotateY(-180deg);
  overflow-y: hidden;
}

.quiz:hover .quiz__question {
  transform: rotateY(180deg);
}

.quiz:hover .quiz__answer  {
  transform: rotateY(25deg); 
  
}

.green .quiz__question, .green
.quiz__answer {
  border-bottom-color: rgb(77, 77, 77);
}

.btn-next{
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  font-weight: bold;
  cursor: pointer;
  font-size: 20px;
 margin-bottom: 20px;
 background-color: #dadada;
}

.card-question{
  margin-top:2%;
}
.btn-div{
  margin-top:7%;
}
.div-main{
  display: flex;
  justify-content: center;
  margin-top: 5%;
}

@media only screen and (max-width: 600px) {
  .div-main{
      margin-top: 25%;
  }
 
}